"use client";
import Image from "next/image";
import banduImg from "../../public/welcome.png";
import { useEffect, useState } from "react";

export default function WelcomeSection() {
  const [isVisible, setIsVisible] = useState(false);

  useEffect(() => {
    const timer = setTimeout(() => {
      setIsVisible(true);
    }, 100); // Delay for fade-in effect
    return () => clearTimeout(timer);
  }, []);

  return (
    <section className="bandu-gradient flex bg-white items-center justify-center min-h-screen w-full overflow-hidden">
      <div
        className={`flex flex-col-reverse md:flex-row items-center max-w-6xl justify-center  px-4 md:px-8 lg:px-16 ${
          isVisible ? "opacity-100" : "opacity-0"
        }`}
      >
        <div className="w-full md:w-2xl text-center md:text-left mt-8 md:mt-0">
          <h1
            className="text-4xl sm:text-5xl md:text-6xl lg:text-[80px] font-medium mb-4 md:mb-0 md:mr-6 leading-[1.3]"
            style={{ fontFamily: "var(--font-inter)" }}
          >
            Banduga{" "}
            <span
              className="interactive-gradient"
              onMouseMove={(e) => {
                const el = e.currentTarget as HTMLElement;
                const rect = el.getBoundingClientRect();
                const x = e.clientX - rect.left;
                const y = e.clientY - rect.top;
                el.style.setProperty("--mx", `${(x / rect.width) * 100}%`);
                el.style.setProperty("--my", `${(y / rect.height) * 100}%`);
              }}
              onMouseLeave={(e) => {
                const el = e.currentTarget as HTMLElement;
                el.style.setProperty("--mx", `50%`);
                el.style.setProperty("--my", `50%`);
              }}
            >
              Hush
            </span>
            <style jsx>{`
              .interactive-gradient {
                position: relative;
                display: inline-block;
                background-image: linear-gradient(
                  90deg,
                  #ffd54a,
                  #ff702d,
                  #000000
                );
                background-size: 200% 200%;
                -webkit-background-clip: text;
                background-clip: text;
                color: transparent;
                animation: hueShift 8s linear infinite;
                transition: transform 0.12s ease;
              }

              .interactive-gradient::after {
                content: "";
                position: absolute;
                inset: 0;
                pointer-events: none;
                background: radial-gradient(
                  circle at var(--mx, 50%) var(--my, 50%),
                  rgba(255, 255, 255, 0.18),
                  transparent 25%
                );
                mix-blend-mode: overlay;
                transition: opacity 0.2s ease;
              }

              @keyframes hueShift {
                0% {
                  background-position: 0% 50%;
                }
                50% {
                  background-position: 100% 50%;
                }
                100% {
                  background-position: 0% 50%;
                }
              }
            `}</style>{" "}
            <br /> Kelibsiz
          </h1>
          <p
            className="mt-2 text-sm sm:text-base md:text-lg lg:text-xl text-[#000000]/80 font-medium leading-relaxed mx-auto md:mx-0"
            style={{
              fontFamily: "var(--font-open-sans)",
              maxWidth: "400px",
              width: "100%",
            }}
          >
            Bandu startap rivojlanish yo'l xaritasi — foydalanuvchi uchun qulay,
            tezkor va samarali xizmatlarni birlashtiruvchi tizim.
          </p>
        </div>
        <div className="relative flex items-center justify-center mb-6 md:mb-0">
          <span className="absolute inset-0 rounded-full blur-2xl bg-yellow-300 opacity-30 scale-90 rounded-tl-full rounded-tr-lg   rounded-bl-lg -z-10"></span>
          <Image
            src={banduImg}
            alt="Bandu Logo"
            width={420}
            height={420}
            className="w-[280px] md:w-[340px] lg:w-[420px] h-auto"
            priority
          />
        </div>
      </div>
    </section>
  );
}